.skills-container {
  padding: 5% 10%;
  display: flex;
  flex-direction: column;
  text-align: left;
}

.skills-container svg {
  color: white;
}

.skills-container .skills-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 50px;
}

.skills-container .skills-grid .skill {
  align-items: center;
  text-align: left;
}

.chip {
  color: rgb(39, 40, 34) !important;
  background-color: white !important;
  font-size: 0.8rem !important;
  height: 25px !important;
}

.chip span {
  font-family: "Courier Prime", monospace;
  color: rgb(39, 40, 34);
}

.chip svg {
  height: 15px !important;
}

.flex-chips {
  line-height: 2;
  margin: 0.5;
}

.flex-chips .chip-title {
  color: white;
  padding-right: 10px;
}

.flex-chips .MuiChip-root {
  margin-right: 0.5em;
}

.flex-chips .MuiChip-outlined {
  background-color: #e0e0e0;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.flex-chips .MuiChip-outlined span {
  color: rgb(39, 40, 34);
}

/* Responsive styles */
@media screen and (max-width: 768px) {
  .skills-container {
    display: block;
    padding-left: 5%;
    padding-right: 5%;
  }

  .skills-container .skills-grid {
    display: initial;
  }

  .skills-container .skills-grid .skill {
    padding-bottom: 5%;
  }
}

@media screen and (max-width: 567px) {
  .skills {
    font-size: 12px;
  }
}
